.container-hidromel{
    height: calc(100vh - 130px);
    display: flex;
    align-items: center;
    justify-content: center;
}

.container-hidromel img{
    width: 30%;
}

.container-hidromel div{
    max-width: 500px;
}

.container-hidromel div h1{
    margin: 20px 0;
}

.container-hidromel div p{
    margin: 20px 0;
    max-width: 80%;
    line-height: 1.5;
}

.container-hidromel div button{
    background-color: #059400;
    color: white;
    width: 150px;
    border-radius: 25px;
    height: 25px;
    border: none;
}