.container-home{
    height: calc(100vh - 130px);
    display: flex;
    align-items: center;
    justify-content: center;
}

.container-home iframe{
    width: 30%;
    margin: 50px;
}

.container-home div{
    max-width: 500px;
}

.container-home div h1{
    margin: 20px 0;
}

.container-home div p{
    margin: 20px 0;
    max-width: 80%;
    line-height: 1.5;
}

.container-home div button{
    background-color: #059400;
    color: white;
    width: 150px;
    border-radius: 25px;
    height: 25px;
    border: none;
}