.container-main{
    background-color: #18242c;
    display: flex;
    justify-content: space-between;
    height: 65px;
    color: white;
    padding: 0 7%;
    align-items: center;

}

.container-main ul{
    list-style: none;
    display: flex;
    align-items: center;
}

.container-main ul li{
    padding: 0 20px;
}

.container-main ul li a{
    text-decoration: none;
    color: white;
}


