.container-sobre{
    height: calc(100vh - 130px);
    display: flex;
    align-items: center;
    justify-content: center;
}

.container-sobre img{
    width: 30%;
    margin: 50px;
}

.container-sobre div{
    max-width: 500px;
}

.container-sobre div h1{
    margin: 20px 0;
}

.container-sobre div p{
    margin: 20px 0;
    max-width: 100%;
    line-height: 1;
}
